import uCharts from '@/common/lib/u-charts.min.js'
import {
	demoData
} from './ucharts-demo-data.js'
var _self;
var canvaColumn = null;
export default {
	data() {
		return {
			cWidth: '',
			cHeight: '',
			pixelRatio: 1,
			serverData: ''
		}
	},
	mounted() {
		_self = this;











		this.cWidth = uni.upx2px(750);
		this.cHeight = uni.upx2px(500);
		this.getServerData();
	},
	methods: {
		getServerData() {
			console.log(demoData.data)
			let Column = {
				categories: [],
				series: []
			};
			//这里我后台返回的是数组，所以用等于，如果您后台返回的是单条数据，需要push进去
			Column.categories = demoData.data.ColumnB.categories;
			Column.series = demoData.data.ColumnB.series;
			_self.textarea = JSON.stringify(demoData.data.ColumnB);
			_self.showColumn("canvasColumn", Column);
		},
		showColumn(canvasId, chartData) {
			canvaColumn = new uCharts({
				$this: _self,
				canvasId: canvasId,
				type: 'column',
				legend: true,
				fontSize: 11,
				background: '#FFFFFF',
				pixelRatio: _self.pixelRatio,
				animation: true,
				categories: chartData.categories,
				series: chartData.series,
				xAxis: {
					disableGrid: true,
				},
				yAxis: {},
				dataLabel: true,
				width: _self.cWidth * _self.pixelRatio,
				height: _self.cHeight * _self.pixelRatio,
				extra: {
					column: {
						width: _self.cWidth * _self.pixelRatio * 0.45 / chartData.categories.length
					}
				}
			});
		},
		changeData() {
			canvaColumn.updateData({
				series: _self.serverData.ColumnB.series,
				categories: _self.serverData.ColumnB.categories
			});
		}
	}
}
